$utility-name: 'u-font-weight';

.#{$utility-name} {
	&-light {
		font-weight: 300 !important;
	}

	&-regular {
		font-weight: 400 !important;
	}

	&-medium {
		font-weight: 500 !important;
	}

	&-semibold {
		font-weight: 600 !important;
	}

	&-bold {
		font-weight: 700 !important;
	}

	&-extrabold {
		font-weight: 800 !important;
	}

	&-black {
		font-weight: 900 !important;
	}
}

//before
.before\:\: {
	&#{$utility-name} {
		&-light {
			&::before {
				font-weight: 300 !important;
			}
		}
	
		&-regular {
			&::before {
				font-weight: 400 !important;
			}
		}
	
		&-medium {
			&::before {
				font-weight: 500 !important;
			}
		}
	
		&-semibold {
			&::before {
				font-weight: .6875rem !important;
			}
		}
	
		&-bold {
			&::before {
				font-weight: 700 !important;
			}
		}
	
		&-extrabold {
			&::before {
				font-weight: 800 !important;
			}
		}
	
		&-black {
			&::before {
				font-weight: 900 !important;
			}
		}
	}    
}

